/* Variables de color si el tema es nulo o light */
html {
    --primary-color: #282c34;
    --secondary-color: #fff;
    --border-color-page: 1px solid rgba(0,0,0,.125);
}

/* Variables de color si el tema es dark */
html[data-theme="dark"] {
    --primary-color: #fff;
    --secondary-color: #282c34;
    --border-color-page: 1px solid rgba(255, 255, 255, 0.12);
}
.pr-3 {
    padding-right: 1rem!important;
}
.pl-3 {
    padding-left: 1rem!important;
}
.accordion-item {
    border: var(--border-color-page);
    transition: all 0.3s linear;
}
.accordion-button:not(.collapsed) {
    color: #388E3C;
}

.accordion-button:focus {
    border-color: #00BCD4;
    box-shadow: 0 0 0 .25rem rgb(0, 188, 212);
}
.nav-link {
    color: var(--primary-color);
}
.btn-theme {
    color: #fff;
    background-color: #388E3C;
    border-color: #388E3C;
}
.form-floating>label{
    color: #282c34;
}
body{
    background-color: var(--secondary-color);
    color: var(--primary-color);
    transition: all 0.3s linear;
}
nav{
    transition: all 0.3s linear;
}
li{
     transition: all 0.3s linear;
 }
.nav_style_new{
    width: 4.5rem;
}
.text-theme-1{
    color: #00BCD4;
}
.text-theme-2{
    color: #388E3C;
}
.carousel-caption {
    position: absolute;
    right: 10%;
    top: 0.15rem;
    left: 10%;
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
}
.only-movil{
    display: inline;
}
.text-nav-decoration{
    text-decoration: none;
}
.only-web{
    display: none;
}
.img-top{
    height: 40px;
}
@media (min-width: 992px) {
    .img-top-md{
        height: 50px;
    }
    .only-movil{
        display: none;
    }
    .only-web{
        display: inline;
    }
}
