/*------------------------------------------------------------------
  [User timeline widget]
*/
.user-timeline {
    padding: 0;
    margin: 5px 0 15px;
    list-style: none;
    position: relative; }
.user-timeline:before {
    content: "";
    position: absolute;
    left: 7px;
    top: 2px;
    height: 100%;
    width: 2px;
    background-color: #4285f4; }
.user-timeline:after {
    content: "";
    position: absolute;
    left: 4px;
    bottom: -2px;
    height: 8px;
    width: 8px;
    background-color: #4285f4;
    border-radius: 50%; }
.user-timeline > li {
    display: block;
    position: relative;
    padding-left: 30px;
    padding-bottom: 27px; }
.user-timeline > li:before {
    content: "";
    position: absolute;
    left: 0;
    top: 2px;
    height: 16px;
    width: 16px;
    background-color: #FFFFFF;
    border-radius: 50%;
    border: 1px solid #4285f4; }
.user-timeline > li.latest:before {
    background-color: #4285f4; }
.user-timeline-date {
    text-transform: uppercase;
    color: #737373;
    margin-bottom: 8px; }
.user-timeline-title {
    font-weight: 500; }
.user-timeline-compact .user-timeline-date {
    float: right;
    text-transform: capitalize;
    margin-bottom: 0;
    line-height: 22px; }
.user-timeline-compact .user-timeline-title {
    font-size: 1.385rem;
    font-weight: 300; }
.user-timeline-compact .user-timeline-description {
    color: #737373;
    margin-top: 4px; }
.user-timeline-compact:before {
    background-color: #f6c163; }
.user-timeline-compact:after {
    background-color: #f6c163; }
.user-timeline-compact > li {
    padding-bottom: 35px; }
.user-timeline-compact > li:last-child {
    padding-bottom: 25px; }
.user-timeline-compact > li:before {
    border: 1px solid #f6c163; }
.user-timeline-compact > li.latest:before {
    background-color: #f6c163; }
/*------------------------------------------------------------------
  [User profile widget]
*/
.user-display {
    margin-bottom: 30px;
    border-radius: 2px;
    background-color: #FFFFFF; }
.user-display-bg {
    max-height: 300px;
    overflow: hidden; }
.user-display-bg img {
    width: 100%;
    border-radius: 2px 2px 0 0; }
@media (max-width: 767.98px) {
    .user-display-bg img {
        min-height: 130px; } }
.user-display-bottom {
    position: relative;
    padding: 11px 20px; }
.user-display-avatar {
    position: absolute;
    left: 10px;
    top: -100px;
    border-radius: 50%;
    background-color: #FFFFFF; }
.user-display-avatar img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    border: 3px solid #FFFFFF; }
.user-display-info {
    line-height: 20px;
    padding-left: 160px; }
.user-display-info .name {
    display: block;
    font-size: 1.385rem;
    font-weight: 500;
    line-height: 22px; }
.user-display-info .nick {
    line-height: 21px;
    color: #5f5f5f; }
.user-display-details {
    margin-top: 15px; }
.user-display-details .title {
    text-transform: uppercase;
    font-size: .9231rem;
    color: #5f5f5f; }
.user-display-details .counter {
    font-size: 1.385rem; }

/*------------------------------------------------------------------
  [User info list widget]
*/
.user-info-list table {
    width: 100%;
    table-layout: fixed; }
.user-info-list table tr td {
    padding: 12px 0; }
.user-info-list table tr td.icon {
    width: 33px; }
.user-info-list table tr td.icon > span {
    font-size: 1.538rem;
    vertical-align: middle;
    margin-bottom: 1px; }
.user-info-list table tr td.item {
    width: 34%; }


.card-title {
    font-size: 18px;
    font-weight: 300;
    display: block;
    margin: 0; }

.card-subtitle {
    display: block;
    font-size: 1rem;
    color: #707070;
    margin-top: 0;
    margin-bottom: 0.4615rem; }

.card-header-divider {
    border-bottom: 1px solid #d9d9d9;
    margin: 0 1.538rem 8px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 1.2307rem; }
.card-header-divider .card-subtitle {
    margin-top: 0;
    margin-bottom: 0; }

.card-flat {
    border: none;
    box-shadow: none; }

.card-border {
    box-shadow: none;
    border: 1px solid #d4d4d4; }
.card-header{
    background-color: rgba(0,0,0,0) !important;
}
/*scrol bar*/
::-webkit-scrollbar{
    width: 10px;
    background-color: #F5F5F5;
}
::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: #F5F5F5;
}
::-webkit-scrollbar-thumb{
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,1);
    background-image: -webkit-linear-gradient(90deg,rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5))
}

